/* Clearing
–––––––––––––––––––––––––––––––––––––––––––––––––– */
/* Self Clearing Goodness */
.container:after,
.row:after,
.u-cf {
  content: "";
  display: table;
  clear: both;
}
/*=== all style ===*/
* {
  box-sizing: border-box;
}
*::selection {
  background: ;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
*:before,
*:after {
  box-sizing: border-box;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
textarea,
button,
input {
  border: 0;
  border-radius: 0;
  font-family: inherit;
}
textarea {
  resize: none;
}
a {
  text-decoration: none;
  transition: all 0.3s;
}
.inner {
  overflow-x: hidden;
}
.content {
  position: relative;
}
[type="submit"] {
  cursor: pointer;
}
.video {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}
.video iframe,
.video object,
.video embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
/* Buttons
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.btn {
  display: inline-block;
  color: #ffffff;
  text-align: center;
  font-size: 15px;
  font-weight: 700;
  width: 200px;
  height: 50px;
  line-height: 50px;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
  user-select: none;
}
.btn_green {
  height: 50px;
  line-height: 50px;
  font-size: 14px;
  background-color: #83ba31;
}
.btn_green:hover {
  background-color: #6c9b25;
}
.btn_green:focus {
  background-color: #6c9b25;
}
.btn_blue {
  background: #4d8acb;
  background: linear-gradient(to right, #4d8acb 0%, #4d9aca 33%, #4dacca 66%, #4dbac9 100%);
}
.btn_invert {
  border-width: 1px;
  border-color: #abaaaa;
  border-style: solid;
  font-size: 15px;
  color: #000000;
  font-weight: bold;
  line-height: 48px;
}
.btn_grey {
  border-width: 1px;
  border-color: #c4c4c4;
  border-style: solid;
  font-size: 15px;
  color: #2a2a2a;
  font-weight: bold;
  line-height: 48px;
}
.overlay {
  display: none;
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  z-index: 9;
}
