.slick-next {
  position: absolute;
  bottom: 0;
  display: block;
  height: 70px;
  width: 70px;
  font-size: 0px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.5);
  color: transparent;
  padding: 0;
  border: none;
