.main-map {
  height: 980px;
}
@media (max-width: 1199px) {
  .main-banner__item {
    position: relative;
  }
  .main-banner__img {
    height: 500px;
  }
  .main-banner__title {
    top: 200px;
    font-size: 32px;
    padding: 0 15px;
  }
  .main-banner__info {
    bottom: 60px;
  }
  .main-banner__info .item__text {
    font-size: 14px;
  }
  .b-filter__left {
    margin: 0 0px;
  }
  .b-filter__right {
    width: 220px;
  }
  .b-filter__right .btn {
    margin-bottom: 20px;
  }
  .b-filter__block {
    display: inline-block;
    vertical-align: top;
    width: 230px;
    margin: 0 10px 20px;
  }
  .b-filter__block_fifty {
    width: 357px;
  }
  .b-filter__checkbox {
    display: inline-block;
    vertical-align: top;
    margin: 0 10px 20px;
  }
  .news__img {
    height: auto;
  }
  .main-resort__title {
    margin-bottom: 30px;
  }
  .main-resort__img {
    height: auto;
  }
  .main-resort__sale {
    top: 5px;
    right: 5px;
  }
  .main-resort__name {
    top: 20%;
  }
}
@media (max-width: 767px) {
  .main-banner__item {
    position: relative;
  }
  .main-banner__img {
    height: 430px;
  }
  .main-banner__title {
    top: 150px;
    font-size: 24px;
  }
  .b-filter {
    padding: 0;
    border-width: 0;
    border-bottom-width: 1px;
    border-color: rgba(172, 172, 172, 0.5);
    border-style: solid;
  }
  .b-filter_internal .b-filter__title {
    display: none;
  }
  .b-filter_internal .b-filter__wrap {
    padding-top: 30px;
    display: block !important;
  }
  .b-filter__close {
    display: block;
    margin: 20px -15px 0;
    font-size: 14px;
    color: #d64c4c;
    text-transform: uppercase;
    font-weight: 900;
    cursor: pointer;
    text-align: right;
  }
  .b-filter__close-btn {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding: 15px;
    padding-right: 35px;
  }
  .b-filter__close-btn:after {
    content: '';
    position: absolute;
    top: 21px;
    right: 20px;
